import React, { useState, useEffect } from "react";

import {
  Panel,
  View,
  Root,
  PanelHeader,
  CellButton,
  CardGrid,
  Card,
  Div
} from "@vkontakte/vkui";

import { GroupIcon } from "./components/GroupIcon";

import bridge from "@vkontakte/vk-bridge";

window.generalTop = null


function App() {
  const [ activePanel, setActivePanel ] = useState("general-top");
  const [ token, setToken ] = useState(null);
  const [ topIsLoaded, setTopStatus] = useState(false);
  
  useEffect(() => {
    getGeneralTop().then(result => {
	  console.log(result);
	  window.generalTop = result;
	  setTopStatus(true);
	}).catch(console.error);
  }, [topIsLoaded]);
  
  async function getGeneralTop() {
	const response = await fetch("https://KitKit.pythonanywhere.com?query=get_general_top");
	return await response.json();
  }
  
  async function addGroup() {
	const group = await bridge.send("VKWebAppAddToCommunity");
	await fetch(`https://KitKit.pythonanywhere.com?query=add_group&vk_group_id=${group.group_id}`);
  }
  
  if (topIsLoaded) {
    return (
      <Root activeView="view">
	    <View activePanel="top" id="view">
	      <Panel id="top">
		    <PanelHeader>Маня автоматизатор</PanelHeader>
			<CellButton centered={ true } onClick={ addGroup }>Добавить сообщество</CellButton>
			<div className="horizontal-center" style={{ marginTop: "1em", marginBottom: "1em" }}>
		      <CardGrid size="1" className="groups">
		        {
			   	  window.generalTop.map(group => {
			        return (
				      <Card mode="shadow" className="group">
					    <Div className="group__info vertical-center">
					   	  <GroupIcon src={ group.photo_50 } />
						  <div style={{ marginLeft: "1em" }}>  
				            <a href={ `https://vk.com/${ group.type == "group" ? "club" : "public" }${ group.id }` }>{ group.name }</a>
			    		    <span>: { group.members_count }</span>
						  </div>
					    </Div>
				      </Card>
				    )
				  })
			    }
		      </CardGrid>
			</div>
		  </Panel>
	    </View>
	  </Root>
    );
  } else {
	return (
	  <Root activeView="view">
	    <View activePanel="top" id="view">
	      <Panel id="top">
		    <PanelHeader>Маня автоматизатор</PanelHeader>
			<CellButton centered={ true } onClick={ addGroup }>Добавить сообщество</CellButton>
		  </Panel>
	    </View>
	  </Root>
	);
  }
}

export default App;
